import cytoscape from 'cytoscape';
import data from './data.json';

const nodes = data.map(item => {
  const prop = Object.keys(item)[0];
  return {
    data: {
      id: prop,
      type: 'node',
    }
  };
}).filter(el => !el.data.id.includes('::'));

const connections = data.reduce((ar, item) => {
  const prop = Object.keys(item)[0];
  if (!!item[prop] && !prop.includes('::')) {
    ar.push(
      ...item[prop].map(assoc => ({
        data: {
          id: `${prop}${assoc.to}`,
          type: 'edge',
          assoc: assoc.assoc,
          source: prop,
          target: assoc.to
        },
      }))
    );
  }
  return ar;
}, []);

if (process.env.NODE_ENV !== 'production') {
  window.nodes = nodes;
  window.connections = connections;
}

const cy = cytoscape({
  container: document.getElementById('cy'),
  elements: [
    ...nodes.filter(el => {
      return !!connections.find(con => (con.data.source === el.data.id) || (con.data.target === el.data.id));
    }),
    ...connections.filter(con => con.data.assoc !== 'HasMany'),
  ],
  style: cytoscape.stylesheet()
    .selector('node').css({
      'shape': 'roundrectangle',
      'width': 'label',
      'height': 'label',
      'padding': 10,
      'background-color': '#eee',
      'border-width': 1,
      'border-color': '#333',
      'label': 'data(id)',
      'text-margin-y': '1.5em',
    })
    .selector('edge').css({
      'width': 1,
      'line-color': '#aaa',
    })
    .selector('edge[assoc="HasMany"]').css({
      'source-arrow-color': '#f00',
      'source-arrow-shape': 'triangle',
    })
    .selector('edge[assoc="BelongsTo"]').css({
      'source-arrow-color': '#0f0',
      'source-arrow-shape': 'triangle',
    }),
  layout: {
    name: 'breadthfirst',
    circle: true,
  },
});

